import { useState, useEffect } from 'react';
import axios from './Api/axios';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTag } from '@fortawesome/free-solid-svg-icons';

const TagRep = () => {

    const [catCl, setCatCl] = useState('telegram_channel');
    const [tagData, setTagData] = useState([]);
    const token = localStorage.getItem('access');

    function turnToTelegramChannel() {
        setCatCl('telegram_channel');
    }

    function turnToTelegramGroups() {
        setCatCl('telegram_groups');
    }

    function turnToInsta() {
        setCatCl('instagram');
    }

    function turnToTweeter() {
        setCatCl('twitter');
    }

    function turnToPost() {
        setCatCl('news_agency');
    }

    const actFilter = {
        backgroundColor: "rgb(1, 31, 75)",
        color: "white",
        borderLeftColor: "rgb(1, 31, 75)",
    };
    const dActFilter = {
    }

    useEffect(() => {
        let isMounted = true;
        const controller = new AbortController();

        const getPie = async () => {
            try {
                const response = await axios.get(`/${catCl}/get_tagging_status`,
                    {
                        headers: { 'Authorization': `Bearer ${token}`, 'Content-Type': 'application/json' }
                    },
                    {
                        signal: controller.signal
                    });
                isMounted && setTagData(response?.data);
                console.log(response);
            } catch (err) {
                console.error(err);
            }
        }

        getPie();
        console.log(tagData);
        return () => {
            isMounted = false;
            isMounted && controller.abort();
        }
    }, [catCl])

    const [typeShow, setTypeShow] = useState()

    return (
        <>
            <h5 className='mt-8 px-4'><span className='inline-block place-content-center align-top text-middle-blue text-xl ml-2'><FontAwesomeIcon icon={faTag} /></span>جزئیات تگ‌های ثبت شده</h5>
            <div className='psyc-container h-80 flex md:block bg-white border-slate-400 rounded-md p-4 mt-4'>
                <nav className='rounded-lg md:max-w-fit h-10 ml-2 md:ml-0 mt-2 md:mb-10' style={{ zIndex: 100000000 }}>
                    <ul className='flex flex-col md:flex-row p-0 m-0'>
                        <li className=' text-white grid place-content-center text-base py-2 px-1 w-28 bg-dark-blue md:rounded-r-md select-none' onClick={() => { setTypeShow(!typeShow) }}>انتخاب بستر</li>
                        <li className={`flex justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 md:mr-1 hover:bg-dark-blue ${typeShow ? "mobVis" : "mobHid"}`} style={catCl === "telegram_channel" ? actFilter : dActFilter} onClick={turnToTelegramChannel}>کانال‌های تلگرام</li>
                        <li className={`flex justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 md:mr-1 hover:bg-dark-blue ${typeShow ? "mobVis" : "mobHid"}`} style={catCl === "telegram_groups" ? actFilter : dActFilter} onClick={turnToTelegramGroups}>گروه‌های تلگرام</li>
                        <li className={`flex justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 md:mr-1 hover:bg-dark-blue ${typeShow ? "mobVis" : "mobHid"}`} style={catCl === "twitter" ? actFilter : dActFilter} onClick={turnToTweeter}>توییتر</li>
                        <li className={`flex justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 md:mr-1 hover:bg-dark-blue ${typeShow ? "mobVis" : "mobHid"}`} style={catCl === "instagram" ? actFilter : dActFilter} onClick={turnToInsta}>اینستاگرام</li>
                        <li className={`flex justify-center items-center text-center text-sm select-none transition-all duration-200 ease-in text-gray-100 bg-middle-blue w-28 py-2 px-1 md:mr-1 hover:bg-dark-blue ${typeShow ? "mobVis" : "mobHid"} md:rounded-l-md`} style={catCl === "news_agency" ? actFilter : dActFilter} onClick={turnToPost}>خبرگذاری‌ها</li>
                    </ul>
                </nav>
                <div className='rounded-lg md:max-w-fit h-10 mt-2 md:mb-1' style={{ zIndex: 100000000 }}>
                    <ul className='flex flex-col md:flex-row p-0 m-0'>
                        <li className=' text-white grid place-content-center text-base py-2 px-1 w-28 bg-dark-blue md:rounded-ss-md cursor-default'>دسته‌بندی</li>
                        <li className={`flex justify-center items-center text-center text-sm cursor-default transition-all duration-200 ease-in text-dark-blue bg-white border-2 border-dark-blue w-28 h-10 py-2 px-1 md:mr-1`}>رکوردهای موجود</li>
                        <li className={`flex justify-center items-center text-center text-sm cursor-default transition-all duration-200 ease-in text-dark-blue bg-white border-2 border-dark-blue w-28 h-10 py-2 px-1 md:mr-1`}>دارای دسته‌بندی</li>
                        <li className={`flex justify-center items-center text-center text-sm cursor-default transition-all duration-200 ease-in text-dark-blue bg-white border-2 border-dark-blue w-28 h-10 py-2 px-1 md:mr-1`}>دارای برچسب</li>
                        <li className={`flex justify-center items-center text-center text-sm cursor-default transition-all duration-200 ease-in text-dark-blue bg-white border-2 border-dark-blue w-28 h-10 py-2 px-1 md:mr-1`}>دارای احساسات</li>
                        <li className={`flex justify-center items-center text-center text-sm cursor-default transition-all duration-200 ease-in text-dark-blue bg-white border-2 border-dark-blue w-28 h-10 py-2 px-1 md:mr-1 md:rounded-se-md`}>موجودیت</li>
                    </ul>
                </div>
                <div className='rounded-lg md:max-w-fit h-10 mt-2 md:mt-0 md:mb-10' style={{ zIndex: 100000000 }}>
                    <ul className='flex flex-col md:flex-row p-0 m-0'>
                        <li className=' text-white grid place-content-center text-base py-2 px-1 w-28 h-10 bg-dark-blue md:rounded-es-md cursor-default'>تعداد تگ‌ها</li>
                        <li className={`flex justify-center items-center text-center text-sm cursor-default transition-all duration-200 ease-in text-dark-blue bg-white border-2 border-dark-blue w-28 h-10 py-2 px-1 md:mr-1`}>{tagData.all_records_in_mongo}</li>
                        <li className={`flex justify-center items-center text-center text-sm cursor-default transition-all duration-200 ease-in text-dark-blue bg-white border-2 border-dark-blue w-28 h-10 py-2 px-1 md:mr-1`}>{tagData.category}</li>
                        <li className={`flex justify-center items-center text-center text-sm cursor-default transition-all duration-200 ease-in text-dark-blue bg-white border-2 border-dark-blue w-28 h-10 py-2 px-1 md:mr-1`}>{tagData.information_service}</li>
                        <li className={`flex justify-center items-center text-center text-sm cursor-default transition-all duration-200 ease-in text-dark-blue bg-white border-2 border-dark-blue w-28 h-10 py-2 px-1 md:mr-1`}>{tagData.sentiment}</li>
                        <li className={`flex justify-center items-center text-center text-sm cursor-default transition-all duration-200 ease-in text-dark-blue bg-white border-2 border-dark-blue w-28 h-10 py-2 px-1 md:mr-1 md:rounded-ee-md`}>{tagData.ner}</li>
                    </ul>
                </div>
            </div>
        </>
    )
}

export default TagRep